import React, { useEffect } from "react";
import "./App.css";
import { ApolloClient, InMemoryCache, ApolloProvider } from "@apollo/client";
import Router from "./router/router.navigation";
import ReactGA from "react-ga";

const TRACKING_ID = process.env.REACT_APP_ANALYTICS_TRACKING_ID; // OUR_TRACKING_ID
const directus_url = process.env.REACT_APP_DIRECTUS_URL;
const access_token = process.env.REACT_APP_DIRECTUS_ACCESS_TOKEN;
const uri = `${directus_url}graphql/?access_token=${access_token}`;

ReactGA.initialize(TRACKING_ID);

const client = new ApolloClient({
  uri: uri,
  cache: new InMemoryCache(),
});

function App() {
  useEffect(() => {
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  return (
    <ApolloProvider client={client}>
      <Router />
    </ApolloProvider>
  );
}

export default App;
